import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createAuction } from "../../actions/authActions";
import classnames from "classnames";
import AuctionNavbar from "./AuctionNavbar";

class CreateAuction extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      type: "",
      initialvalue: "",
      finalvalue: "",
      errors: {}
    };
  }

  componentDidMount() {
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/createauction");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();

    const newAuction = {
      name: this.state.name,
      type: this.state.type,
      initialvalue: this.state.initialvalue,
      finalvalue: this.state.finalvalue
    };

    this.props.createAuction(newAuction, this.props.history);
  };

  render() {
    const { errors } = this.state;

    return (<div>

          <AuctionNavbar></AuctionNavbar>
      <div className="container">
        <div className="row">
          <div className="col s8 offset-s2">
            <div className="col s12" style={{ paddingLeft: "11.250px" }}>
              <h4>Create New Auction Item              </h4>
            </div>
            <form noValidate onSubmit={this.onSubmit}>
              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.name}
                  error={errors.name}
                  id="name"
                  type="text"
                  className={classnames("", {
                      invalid: errors.name
                    })}
                    />
                <label htmlFor="name">Name</label>
                <span className="red-text">{errors.name}</span>
              </div>
              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.type}
                  error={errors.type}
                  id="type"
                  type="text"
                  className={classnames("", {
                      invalid: errors.type
                    })}
                    />
                <label htmlFor="email">Type</label>
                <span className="red-text">{errors.email}</span>
              </div>
              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.initialvalue}
                  error={errors.initialvalue}
                  id="initialvalue"
                  type="text"
                  className={classnames("", {
                      invalid: errors.initialvalue
                    })}
                    />
                <label htmlFor="initialvalue">Initial Value</label>
                <span className="red-text">{errors.initialvalue}</span>
              </div>
              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.finalvalue}
                  error={errors.finalvalue}
                  id="finalvalue"
                  type="text"
                  className={classnames("", {
                      invalid: errors.finalvalue
                    })}
                    />
                <label htmlFor="finalvalue">Final Value</label>
                <span className="red-text">{errors.finalvalue}</span>
              </div>
              
              <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                <button
                  style={{
                      width: "150px",
                      borderRadius: "3px",
                      letterSpacing: "1.5px",
                      marginTop: "1rem"
                    }}
                    type="submit"
                    className="btn btn-large waves-effect waves-light hoverable blue accent-3"
                >
                  Create New Auction Item
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
</div>
    );
  }
}

CreateAuction.propTypes = {
    createAuction: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createAuction }
)(withRouter(CreateAuction));
