import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { listAuctions,deleteAuction } from "../../actions/authActions";
import axios from "axios";
import AuctionNavbar from './AuctionNavbar';

const mapStateToProps = state => ({
    auctions: state.auctions
  });

class ListAuction extends Component {
    constructor(props) {
        super(props);
        this.state = {
          auctions: [],
          auctionId:0,
          errors: {}
        };
      }

      handleAnchorClick = e =>{
        console.log(e.target.id);
          var deleteAuctionData = {
            id: e.target.id
          };
      
          this.props.deleteAuction(deleteAuctionData, this.props.history);
      }
      
    
      componentDidMount(){
        axios
        .get("/api/users/auctions")
        .then(res => {
            console.log(res.data);
            this.setState({ auctions: res.data });
        }).catch();
      }

    render() {
        const auctions = this.state.auctions.map((item, i) => (
            <div className="list-group-item">
              <h4 className="list-group-item-heading">Item name : { item.name}</h4>
              <p className="list-group-item-text"><b>Item Type</b> : { item.type }, <b>Item Initial Value :</b> { item.initialvalue }, <b>Item Final Value :</b> { item.finalvalue }</p>
              <form  onSubmit={this.onSubmit}>
              <input
              onChange ={this.handleAuctionIdChange}
                  value={item._id}
                  id="itemid"
                  type="hidden"
                    />
              <button
                    type="submit"
                    className="btn btn-primary waves-effect waves-light hoverable blue accent-3"
                >
                  Delete
                </button>
                <a href="#" className="btn btn-primary" id={item._id} value={item._id} onClick={this.handleAnchorClick}>Delete</a>
              </form>
            </div>
          ));

          return (<div>

                          <AuctionNavbar></AuctionNavbar>
            <div id="layout-content" className="container">
                <h1>List of Auction Items</h1>
                <hr></hr>
              <div className="list-group">{ auctions }</div>
            </div>
          </div>
          );
    }
}

ListAuction.propTypes = {
    listAuctions: PropTypes.func.isRequired,
    deleteAuction: PropTypes.func.isRequired
};

export default connect(
    mapStateToProps,{listAuctions,deleteAuction}
)(ListAuction);